import React , { useState }  from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Alert from '@material-ui/lab/Alert';
import {callApi} from './callApi'
import {Router, Redirect } from "react-router-dom";
import { createHashHistory } from 'history'
const history = createHashHistory()


const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));





export default function SignIn() {
  const classes = useStyles();
      const [key , setKey ] = useState();
      const [checkKey , setCheckKey  ] = useState(false);
      const [loginDone , setLoginDone ] = useState(false);

    const handleSubmit = async (evt) => {
        evt.preventDefault();       
        !key ? setCheckKey(true)  : setCheckKey(false) 
       if(key){ 
        callApi("?keyAdmin="+key).then(async(response) => {
            console.log(response.data)
            if(response.data.length === 0){
                alert("Incorrect Key")
            } else {
               await localStorage.setItem("adminShopSale", JSON.stringify(response.data[0]));
              //  window.open( 'https://longbody.github.io/shopsaleadmin/#/shopsaleadmin/dashboard')
            }
        })
       }     
    }

    return (
        <Container component="main" maxWidth="xs">
          <CssBaseline />
          <div className={classes.paper}>
            <Typography component="h1" variant="h5">
              Admin DashBoard
            </Typography>
            <form className={classes.form} noValidate
            onSubmit={handleSubmit}
            >
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                label="Key admin"
                name="email"
                type="password"
                autoFocus
                value={key}
                onChange={e => setKey(e.target.value)}
              />
    
              {
                  checkKey ? <Alert severity="error" >Missing Key!</Alert>
                  : ""
              }
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
              >
                Enter
              </Button>
            </form>
          </div>
        </Container>
      );
 

}